.navbar {
    width: 100%;
    height: 80px;
    background-color: rgb(19, 19, 19);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.links {
    margin-right: 50px;
    display: flex;
    align-items: center;
}

#logo .links {
    display: flex;
    justify-content: start;
    align-items: center;
    display: flex;
}

.links a {
    text-decoration: none;
    color: white;
    margin-left: 20px;
    font-size: 25px;
}